html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "NeoSansProCyr", "NeoSansProCyr-Regular";
  color: #333;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  background-color: var(--kiwi);
  color: #fff;
  padding: 16px 28px;
  cursor: pointer;
  text-align: center;
  width: 190px;
  border-radius: 4px;
  display: block;
}

.btn:hover {
  background-color: #beb400c0;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: fixed;
  width: 100%;
  z-index: 999;
  top: 0;
  left: 0;
}

.header__inner {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  padding-right: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__menu-list {
  display: flex;
}

.header__menu-link {
  padding-right: 25px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}

.header__menu-link:last-child {
  padding-right: 0;
}

.header__menu-link a {
  position: relative;
  line-height: 1;
}

.header__menu-link a::after {
  display: block;
  position: absolute;
  content: "";
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--kiwi);
  transition: width 0.3s ease-out;
}

.header__menu-link a:hover::after {
  width: 100%;
}

.header__btn {
  color: #223880;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  border: 1px solid #78A7CD;
  border-radius: 4px;
  background-color: #fff;
  padding: 10px 12px;
  cursor: pointer;
  transition: all .5 ease-in;
}

.header__btn:hover {
  background-color: #223880;
  color: #fff;
}

.burger {
  --burger-width: 40px;
  --burger-height: 20px;
  --burger-line-height: 2px;
  display: none;
  width: var(--burger-width);
  height: var(--burger-height);
  position: relative;
  color: #000;
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out;
}

.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger::before {
  top: 0;
}

.burger::after {
  top: calc(100% - var(--burger-line-height));
}

.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger--active::before {
  transform: rotate(45deg);
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger--active::after {
  transform: rotate(-45deg);
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.main {
  height: 100vh;
  background-image: url(../img/main-bg.jpg);
  background-position: top center;
  background-size: cover;
  position: relative;
  width: 100%;
}

.main-container {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.main__info-title {
  color: #fff;
  font-weight: 400;
  margin-bottom: 15px;
  font-size: calc(32px + 28 * ((100vw - 320px) / 1200));
}

.main__info-subtitle {
  color: #fff;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 195px;
}

.main__info-row {
  display: flex;
  justify-content: space-between;
}

.main__info-item {
  background: rgba(13, 13, 13, 0.5);
  backdrop-filter: blur(8px);
  border-radius: 8px;
}

.main__info-item.banner {
  max-width: 200px;
}

.main__info-form {
  padding: 16px 24px;
  max-width: 465px;
  width: 100%;
}

.main__info-form__country {
  width: 100%;
  max-width: 415px;
}

.main__info-form__date {
  width: 200px;
}

.main__info-form label {
  color: #fff;
  font-size: 12px;
  line-height: 16px;
}

.main__info-line {
  display: flex;
  justify-content: space-between;
}

.main__info-btn {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  background-color: var(--kiwi);
  color: #fff;
  padding: 16px 28px;
  cursor: pointer;
  text-align: center;
  width: 190px;
  border-radius: 4px;
  display: block;
}

.main__info-btn:hover {
  background-color: #beb400c0;
}

.main__info-price {
  font-size: 24px;
  line-height: 48px;
  color: #fff;
}

.main__info-price span {
  font-size: 40px;
}

.main__info-content {
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
}

.main__info-content p {
  font-size: 12px;
  line-height: 16px;
  color: var(--kiwi);
}

.main__info-content span {
  font-size: 15px;
  line-height: 24px;
  display: block;
  color: #fff;
  margin-bottom: 25px;
}

.main__info-content span:last-child {
  margin-bottom: 0;
}

.main__info-content img {
  display: block;
}

.main__info-banner {
  padding-top: 110px;
}

.main__info-banner img {
  cursor: pointer;
}

.form__search {
  margin-bottom: 15px;
}

.form__search label {
  position: relative;
}

.form__search label::after {
  content: "";
  position: absolute;
  background-image: url(../img/location.svg);
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  left: 0;
  top: 40%;
  transform: translate(50%, 50%);
}

.form__search-input {
  height: 56px;
  max-width: 416px;
  width: 100%;
  position: relative;
  padding: 0 50px;
  margin-top: 5px;
}

.form__search-input::placeholder {
  font-size: 18px;
  line-height: 24px;
  color: #333;
  background: #EBEBEB;
  border-radius: 4px;
  padding: 4px 32px 8px 4px;
  width: 97px;
}

.form__row {
  display: flex;
  margin-bottom: 45px;
}

.insurance {
  padding: 128px 0;
  background-color: #fff;
}

.insurance__title {
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 65px;
}

.insurance__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.insurance__item {
  width: 20%;
  position: relative;
}

.insurance__item::after {
  content: "";
  position: absolute;
  border: 1px solid var(--kiwi);
  width: 64px;
  bottom: -30px;
}

.insurance__item-img {
  margin-bottom: 32px;
}

.insurance__item-title {
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 30px;
}

.insurance__item-text {
  font-size: 14px;
  line-height: 20px;
  font-weight: 300;
}

.information {
  background: #F4F4F4;
  padding: 128px 0;
}

.information__inner {
  display: flex;
  justify-content: space-between;
  margin-bottom: 128px;
}

.information__inner:last-child {
  margin-bottom: 0;
}

.information__item {
  max-width: 560px;
  width: 100%;
}

.information__item span {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 24px;
  font-weight: 400;
  margin-bottom: 24px;
  display: block;
}

.information__item-title {
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 24px;
}

.information__item-text {
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 24px;
}

.information__item-text:last-child {
  margin-bottom: 0;
}

.information__item-list {
  padding-left: 44px;
  margin-bottom: 16px;
}

.information__item-link {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 5px;
  position: relative;
}

.information__item-link::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: var(--kiwi);
  left: -15px;
  top: 8px;
}

.information__item-subtitle {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 15px;
}

.information__image img {
  max-width: 100%;
}

.information.info {
  background-color: #fff;
}

.information__btn {
  width: 274px;
  display: flex;
  margin: 0 auto;
}

.advantages {
  background-image: url(../img/advantages-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 604px;
  padding: 74px 0 90px;
}

.advantages__title {
  text-align: center;
  font-size: 56px;
  line-height: 68px;
  color: #78A7CD;
  margin-bottom: 75px;
}

.advantages__inner {
  display: flex;
  justify-content: space-between;
}

.advantages__item {
  width: 240px;
}

.advantages__item-image {
  margin-bottom: 32px;
}

.advantages__item-title {
  font-size: 24px;
  line-height: 32px;
  color: #fff;
  margin-bottom: 24px;
}

.advantages__item-text {
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
}

.country {
  padding: 129px 0 140px;
}

.country__title {
  font-size: 48px;
  line-height: 56px;
  margin-bottom: 56px;
}

.country__inner {
  display: flex;
  justify-content: space-between;
}

.country__content {
  max-width: 752px;
  width: 100%;
}

.country__content-img {
  margin-bottom: 40px;
}

.country__content-img img {
  width: 100%;
}

.country__content-text {
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 30px;
}

.country__content-text:last-child {
  margin-bottom: 0;
}

.country__content-info {
  padding-left: 112px;
}

.country__content-info__item {
  margin-bottom: 30px;
}

.country__content-info__item p {
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: var(--kiwi);
}

.country__content-info__item span {
  font-size: 15px;
  line-height: 24px;
  display: block;
}

.request {
  background-color: #F4F4F4;
  padding: 95px 0;
}

.request__row {
  display: flex;
  justify-content: space-between;
}

.request__row-item {
  display: flex;
  align-items: center;
