.request__text {
  margin-left: 30px;
  font-size: 18px;
  line-height: 24px;
}

.request__btn {
  margin-left: 30px;
  background-color: var(--kiwi);
  color: #fff;
  border: none;
  padding: 14px 16px;
  cursor: pointer;
}

.request__btn:hover {
  background-color: #beb400c0;
}

.about {
