}

.footer__logo {
  margin-top: 20px;
}

.footer__item-title {
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 12px;
}

.footer__item-link {
  color: #223880;
  margin-bottom: 4px;
  font-weight: 300;
}

.footer__contact {
  margin-bottom: 32px;
