.footer__copyright {
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
  color: #a0a0a0;
  margin-bottom: 20px;
}

@media (max-width: 1200px) {
  .main__info {
    padding-top: 120px;
  }
}

@media (max-width: 1199px) {
  .country {
    padding: 64px 0 72px;
  }
  .request__row-item::before {
    left: -35px;
  }
  .about {
    padding: 64px 0 128px;
  }
  .about__inner {
    margin-bottom: 84px;
  }
  .about__title {
    font-size: 48px;
    margin-bottom: 40px;
  }
  .about__text {
