html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "NeoSansProCyr", "NeoSansProCyr-Regular";
  color: #333;
  font-weight: 400;
}

// img {
//   max-width: 100%;
// }

a {
  text-decoration: none;
  color: inherit;
}
p{
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6{
  margin: 0;
  padding: 0;
}


.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  @include stop-scroll;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.visually-hidden {
  @include visually-hidden;
}

.btn{
  font-weight:700;
  font-size: 18px;
  line-height: 24px;
  background-color: var(--kiwi);
  color: #fff;
  padding: 16px 28px;
  cursor: pointer;
  text-align: center;
  width: 190px;
  border-radius: 4px;
  display: block;
  &:hover{
      background-color: #beb400c0;;
  }
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
